    'è´Ÿè´£äºº': string;    // ç”¨æˆ·æ›´æ–°åçš„å­—æ®µåï¼Œç›´æ¥å¯¹åº”è´Ÿè´£äºº
    // ä»¥ä¸‹ä¸ºå¯é€‰å­—æ®µï¼Œå¦‚æœç”¨æˆ·è¡¨æ ¼ä¸­æ²¡æœ‰åˆ™ä½¿ç”¨é»˜è®¤å€¼
    'æœºæ„åœ°å€'?: string;
