declare global {
  interface Window {
    h5sdk: any;
  }
}

export interface PluginConfig {
  width?: number;
  height?: number;
  data?: any;
}

export class FeishuPluginService {
  private static instance: FeishuPluginService;
  private isInitialized = false;

  private onConfirmCallback?: (data: any) => void;
  private onCancelCallback?: () => void;

  private constructor() {}

  public static getInstance(): FeishuPluginService {
    if (!FeishuPluginService.instance) {
      FeishuPluginService.instance = new FeishuPluginService();
    }
    return FeishuPluginService.instance;
  }

  /**
   * åˆå§‹åŒ–é£ä¹¦æ’ä»¶
   */
  public async initialize(): Promise<void> {
    if (this.isInitialized) {
      return;
    }

    try {
      // æ£€æŸ¥æ˜¯å¦åœ¨é£ä¹¦ç¯å¢ƒä¸­
      if (typeof window.h5sdk === 'undefined') {
        console.warn('é£ä¹¦ SDK æœªåŠ è½½ï¼Œä½¿ç”¨æ¼”ç¤ºæ¨¡å¼');
        this.initDemoMode();
        return;
      }

      // åˆå§‹åŒ–é£ä¹¦ SDK
      await window.h5sdk.ready();
      
      // è·å–æ’ä»¶é…ç½®
      const config = await this.getPluginConfig();
      console.log('æ’ä»¶é…ç½®:', config);

      // æ˜¾ç¤ºç¡®è®¤æŒ‰é”®
      this.showActionButtons();

      // è®¾ç½®æ’ä»¶äº‹ä»¶ç›‘å¬
      this.setupEventListeners();

      this.isInitialized = true;
      console.log('é£ä¹¦æ’ä»¶åˆå§‹åŒ–æˆåŠŸ');

    } catch (error) {
      console.error('é£ä¹¦æ’ä»¶åˆå§‹åŒ–å¤±è´¥:', error);
      this.initDemoMode();
    }
  }

  /**
   * åˆå§‹åŒ–æ¼”ç¤ºæ¨¡å¼
   */
  private initDemoMode(): void {
    console.log('å¯åŠ¨æ¼”ç¤ºæ¨¡å¼');
    this.isInitialized = true;
    
    // åœ¨æ¼”ç¤ºæ¨¡å¼ä¸‹ä¹Ÿæ˜¾ç¤ºç¡®è®¤æŒ‰é”®
    setTimeout(() => {
      this.showActionButtons();
    }, 1000);
  }

  /**
   * è·å–æ’ä»¶é…ç½®
   */
  private async getPluginConfig(): Promise<PluginConfig> {
    try {
      if (window.h5sdk && window.h5sdk.bitable) {
        // è·å–å¤šç»´è¡¨æ ¼æ•°æ®
        const selection = await window.h5sdk.bitable.base.getSelection();
        return {
          data: selection,
          width: 800,
          height: 600
        };
      }
    } catch (error) {
      console.error('è·å–æ’ä»¶é…ç½®å¤±è´¥:', error);
    }

    return {
      width: 800,
      height: 600
    };
  }

  /**
   * æ˜¾ç¤ºç¡®è®¤æŒ‰é”®
   */
  private showActionButtons(): void {
    const actionContainer = document.getElementById('plugin-actions');
    if (actionContainer) {
      actionContainer.style.display = 'block';
    }
  }

  /**
   * éšè—ç¡®è®¤æŒ‰é”®
   */
  private hideActionButtons(): void {
    const actionContainer = document.getElementById('plugin-actions');
    if (actionContainer) {
      actionContainer.style.display = 'none';
    }
  }

  /**
   * è®¾ç½®äº‹ä»¶ç›‘å¬å™¨
   */
  private setupEventListeners(): void {
    const confirmBtn = document.getElementById('confirm-btn');
    const cancelBtn = document.getElementById('cancel-btn');

    if (confirmBtn) {
      confirmBtn.addEventListener('click', () => {
        this.handleConfirm();
      });
    }

    if (cancelBtn) {
      cancelBtn.addEventListener('click', () => {
        this.handleCancel();
      });
    }
  }

  /**
   * å¤„ç†ç¡®è®¤æ“ä½œ
   */
  private async handleConfirm(): Promise<void> {
    try {
      console.log('ç”¨æˆ·ç‚¹å‡»ç¡®è®¤');

      // è·å–å½“å‰æ’ä»¶æ•°æ®
      const currentData = this.getCurrentPluginData();

      // è°ƒç”¨ç¡®è®¤å›è°ƒ
      if (this.onConfirmCallback) {
        this.onConfirmCallback(currentData);
      }

      // å¦‚æœåœ¨é£ä¹¦ç¯å¢ƒä¸­ï¼Œä¿å­˜æ•°æ®å¹¶å…³é—­æ’ä»¶
      if (window.h5sdk) {
        await this.savePluginData(currentData);
        await window.h5sdk.bridge.close();
      } else {
        // æ¼”ç¤ºæ¨¡å¼ä¸‹çš„å¤„ç†
        this.showMessage('é…ç½®å·²ä¿å­˜ï¼ˆæ¼”ç¤ºæ¨¡å¼ï¼‰', 'success');
        this.hideActionButtons();
      }

    } catch (error) {
      console.error('ç¡®è®¤æ“ä½œå¤±è´¥:', error);
      this.showMessage('ä¿å­˜å¤±è´¥ï¼Œè¯·é‡è¯•', 'error');
    }
  }

  /**
   * å¤„ç†å–æ¶ˆæ“ä½œ
   */
  private async handleCancel(): Promise<void> {
    try {
      console.log('ç”¨æˆ·ç‚¹å‡»å–æ¶ˆ');

      // è°ƒç”¨å–æ¶ˆå›è°ƒ
      if (this.onCancelCallback) {
        this.onCancelCallback();
      }

      // å¦‚æœåœ¨é£ä¹¦ç¯å¢ƒä¸­ï¼Œç›´æ¥å…³é—­æ’ä»¶
      if (window.h5sdk) {
        await window.h5sdk.bridge.close();
      } else {
        // æ¼”ç¤ºæ¨¡å¼ä¸‹çš„å¤„ç†
        this.showMessage('å·²å–æ¶ˆé…ç½®', 'info');
        this.hideActionButtons();
      }

    } catch (error) {
      console.error('å–æ¶ˆæ“ä½œå¤±è´¥:', error);
    }
  }

  /**
   * è·å–å½“å‰æ’ä»¶æ•°æ®
   */
  private getCurrentPluginData(): any {
    // è¿™é‡Œåº”è¯¥æ”¶é›†å½“å‰æ’ä»¶çš„é…ç½®æ•°æ®
    // æ¯”å¦‚åœ°å›¾é…ç½®ã€æ•°æ®æºè®¾ç½®ç­‰
    return {
      timestamp: Date.now(),
      config: {
        dataSource: localStorage.getItem('feishu-map-datasource') || 'demo',
        regionMapping: JSON.parse(localStorage.getItem('feishu-map-regions') || '{}'),
        colorScheme: localStorage.getItem('feishu-map-colors') || 'default'
      }
    };
  }

  /**
   * ä¿å­˜æ’ä»¶æ•°æ®
   */
  private async savePluginData(data: any): Promise<void> {
    if (window.h5sdk && window.h5sdk.bitable) {
      try {
        // ä¿å­˜åˆ°é£ä¹¦å¤šç»´è¡¨æ ¼æˆ–å…¶ä»–å­˜å‚¨
        console.log('ä¿å­˜æ’ä»¶æ•°æ®:', data);
        // è¿™é‡Œå¯ä»¥å®ç°å…·ä½“çš„æ•°æ®ä¿å­˜é€»è¾‘
      } catch (error) {
        console.error('ä¿å­˜æ•°æ®å¤±è´¥:', error);
        throw error;
      }
    }
  }

  /**
   * æ˜¾ç¤ºæ¶ˆæ¯æç¤º
   */
  private showMessage(message: string, type: 'success' | 'error' | 'info' = 'info'): void {
    // åˆ›å»ºæ¶ˆæ¯æç¤ºå…ƒç´ 
    const messageEl = document.createElement('div');
    messageEl.style.cssText = `
      position: fixed;
      top: 20px;
      left: 50%;
      transform: translateX(-50%);
      padding: 12px 24px;
      border-radius: 4px;
      color: white;
      font-size: 14px;
      z-index: 10000;
      background: ${type === 'success' ? '#52c41a' : type === 'error' ? '#ff4d4f' : '#1890ff'};
    `;
    messageEl.textContent = message;

    document.body.appendChild(messageEl);

    // 3ç§’åè‡ªåŠ¨ç§»é™¤
    setTimeout(() => {
      if (messageEl.parentNode) {
        messageEl.parentNode.removeChild(messageEl);
      }
    }, 3000);
  }

  /**
   * è®¾ç½®ç¡®è®¤å›è°ƒ
   */
  public onConfirm(callback: (data: any) => void): void {
    this.onConfirmCallback = callback;
  }

  /**
   * è®¾ç½®å–æ¶ˆå›è°ƒ
   */
  public onCancel(callback: () => void): void {
    this.onCancelCallback = callback;
  }

  /**
   * æ£€æŸ¥æ˜¯å¦åœ¨é£ä¹¦ç¯å¢ƒä¸­
   */
  public isInFeishuEnvironment(): boolean {
    return typeof window.h5sdk !== 'undefined';
  }

  /**
   * è·å–é£ä¹¦ç”¨æˆ·ä¿¡æ¯
   */
  public async getUserInfo(): Promise<any> {
    if (window.h5sdk && window.h5sdk.bitable) {
      try {
        return await window.h5sdk.bitable.bridge.getUserInfo();
      } catch (error) {
        console.error('è·å–ç”¨æˆ·ä¿¡æ¯å¤±è´¥:', error);
        return null;
      }
    }
    return null;
  }

  /**
   * è·å–å¤šç»´è¡¨æ ¼æ•°æ®
   */
  public async getBitableData(): Promise<any> {
    if (window.h5sdk && window.h5sdk.bitable) {
      try {
        const selection = await window.h5sdk.bitable.base.getSelection();
        if (selection && selection.tableId) {
          const table = await window.h5sdk.bitable.base.getTableById(selection.tableId);
          const records = await table.getRecords();
          return records;
        }
      } catch (error) {
        console.error('è·å–å¤šç»´è¡¨æ ¼æ•°æ®å¤±è´¥:', error);
      }
    }
    return null;
  }
}

// å¯¼å‡ºå•ä¾‹å®ä¾‹
export const feishuPluginService = FeishuPluginService.getInstance();