import { create } from 'zustand';
import { devtools, persist } from 'zustand/middleware';
import type { RegionData, ColorScheme, LoadingState, FilterOptions, Statistics, UserProfile } from '@/types';
import { mockRegionData } from '@/data/chinaMap';
import { updateColorSchemeMaxValues, calculateRegionColor } from '@/utils/colorUtils';
import { calculateStatistics, sortRegions } from '@/utils/dataTransform';
import { FeishuBitableService } from '@/services/feishuBitableService';

// åº”ç”¨è‡ªå®šä¹‰åŒºåŸŸæ˜ å°„çš„è¾…åŠ©å‡½æ•°
const applyRegionMapping = (regions: RegionData[], regionMapping: Record<string, string[]>): RegionData[] => {
  return regions.map(region => {
    // æ ¹æ®çœä»½åç§°æŸ¥æ‰¾å¯¹åº”çš„ç®¡ç†åŒºåŸŸ
    const newRegionType = Object.entries(regionMapping).find(([, provinces]) => 
      provinces.includes(region.name)
    )?.[0];
    
    if (newRegionType && newRegionType !== region.regionType) {
      return {
        ...region,
        regionType: newRegionType
      };
    }
    
    return region;
  });
};

interface MapState {
  // æ•°æ®çŠ¶æ€
  regions: RegionData[];
  colorSchemes: Record<string, ColorScheme>;
  statistics: Statistics | null;
  
  // UI çŠ¶æ€
  selectedRegion: RegionData | null;
  hoveredRegion: string | null;
  showControls: boolean;
  showDetailModal: boolean;
  isFullscreen: boolean;
  
  // ç­›é€‰å’Œæœç´¢
  filterOptions: FilterOptions;
  searchTerm: string;
  sortBy: 'name' | 'institutionCount' | 'manager';
  sortOrder: 'asc' | 'desc';
  
  // åŠ è½½çŠ¶æ€
  loadingState: LoadingState;
  
  // ç”¨æˆ·ä¿¡æ¯
  userProfile: UserProfile | null;
  
  // å¤šç»´è¡¨æ ¼é…ç½®
  bitableConfig: {
    appToken: string;
    tableId: string;
    accessToken: string;
  } | null;
  
  // æ•°æ®æºç±»å‹
  dataSource: 'mock' | 'bitable';
  
  // è‡ªå®šä¹‰åŒºåŸŸæ˜ å°„
  regionMapping: Record<string, string[]> | null;
}

interface MapActions {
  // æ•°æ®æ“ä½œ
  loadRegions: () => Promise<void>;
  loadFromBitable: (config: { appToken: string; tableId: string; accessToken: string }) => Promise<void>;
  updateRegion: (regionId: string, updates: Partial<RegionData>) => void;
  refreshData: () => Promise<void>;
  switchDataSource: (source: 'mock' | 'bitable') => void;
  
  // UI æ“ä½œ
  setSelectedRegion: (regionId: string | null) => void;
  setHoveredRegion: (regionId: string | null) => void;
  toggleControls: () => void;
  toggleDetailModal: (show?: boolean) => void;
  toggleFullscreen: () => void;
  
  // ç­›é€‰å’Œæœç´¢
  setFilterOptions: (options: Partial<FilterOptions>) => void;
  setSearchTerm: (term: string) => void;
  setSorting: (sortBy: MapState['sortBy'], order?: MapState['sortOrder']) => void;
  clearFilters: () => void;
  
  // è®¡ç®—æ–¹æ³•
  getFilteredRegions: () => RegionData[];
  getRegionColor: (regionType: string, institutionCount: number) => string;
  
  // ç”¨æˆ·æ“ä½œ
  setUserProfile: (profile: UserProfile | null) => void;
  
  // åŒºåŸŸæ˜ å°„ç®¡ç†
  setRegionMapping: (mapping: Record<string, string[]>) => void;
  getProvinceRegion: (province: string) => string | null;
  
  // é”™è¯¯å¤„ç†
  setError: (error: string | null) => void;
  clearError: () => void;
}

type MapStore = MapState & MapActions;

const initialState: MapState = {
  regions: [],
  colorSchemes: {},
  statistics: null,
  selectedRegion: null,
  hoveredRegion: null,
  showControls: true,
  showDetailModal: false,
  isFullscreen: false,
  filterOptions: {},
  searchTerm: '',
  sortBy: 'name',
  sortOrder: 'asc',
  loadingState: {
    isLoading: false,
    error: null,
    lastUpdated: null
  },
  userProfile: null,
  bitableConfig: {
    appToken: 'PGuTbyauXavdl7s8Jpec2lUAnah',
    tableId: 'tblLawFGl4bUcqHA',
    accessToken: 't-g10495bGB7VY3MTCZ2IWG6WD7DUIX74OD2RLW26I'
  },
  dataSource: 'bitable',  // é»˜è®¤ä½¿ç”¨å¤šç»´è¡¨æ ¼æ•°æ®æº
  regionMapping: null
};

export const useMapStore = create<MapStore>()(
  devtools(
    persist(
      (set, get) => ({
        ...initialState,

        // æ•°æ®æ“ä½œ
        loadRegions: async () => {
          const { dataSource, bitableConfig, regionMapping } = get();
          
          set((state) => ({
            loadingState: { ...state.loadingState, isLoading: true, error: null }
          }));

          try {
            let regions: RegionData[];

            if (dataSource === 'bitable' && bitableConfig) {
              // ä»å¤šç»´è¡¨æ ¼åŠ è½½æ•°æ®
              const bitableService = new FeishuBitableService(
                bitableConfig.appToken,
                bitableConfig.tableId,
                bitableConfig.accessToken
              );
              
              const institutionRecords = await bitableService.getInstitutionRecords();
              const managerRecords = await bitableService.getManagerRecords();
              regions = bitableService.transformToRegionData(institutionRecords, managerRecords, regionMapping);
            } else {
              // ä½¿ç”¨æ¨¡æ‹Ÿæ•°æ®ï¼Œå¦‚æœæœ‰è‡ªå®šä¹‰åŒºåŸŸæ˜ å°„åˆ™åº”ç”¨
              await new Promise(resolve => setTimeout(resolve, 1000));
              regions = regionMapping ? applyRegionMapping(mockRegionData, regionMapping) : mockRegionData;
            }
            
            const colorSchemes = updateColorSchemeMaxValues(regions);
            const statistics = calculateStatistics(regions);

            set({
              regions,
              colorSchemes,
              statistics,
              loadingState: {
                isLoading: false,
                error: null,
                lastUpdated: Date.now()
              }
            });
          } catch (error) {
            set((state) => ({
              loadingState: {
                ...state.loadingState,
                isLoading: false,
                error: {
                  code: 'LOAD_ERROR',
                  message: error instanceof Error ? error.message : 'åŠ è½½æ•°æ®å¤±è´¥',
                  details: error instanceof Error ? { message: error.message } : undefined,
                  timestamp: Date.now()
                }
              }
            }));
          }
        },

        loadFromBitable: async (config) => {
          set({
            bitableConfig: config,
            dataSource: 'bitable'
          });
          
          const { loadRegions } = get();
          await loadRegions();
        },

        updateRegion: (regionId: string, updates: Partial<RegionData>) => {
          set((state) => {
            const updatedRegions = state.regions.map(region =>
              region.id === regionId ? { ...region, ...updates } : region
            );
            
            return {
              regions: updatedRegions,
              statistics: calculateStatistics(updatedRegions),
              colorSchemes: updateColorSchemeMaxValues(updatedRegions)
            };
          });
        },

        refreshData: async () => {
          const { loadRegions } = get();
          await loadRegions();
        },

        switchDataSource: (source) => {
          set({ dataSource: source });
        },

        // UI æ“ä½œ
        setSelectedRegion: (regionId: string | null) => {
          const region = regionId ? get().regions.find(r => r.id === regionId || r.name === regionId) : null;
          set({ selectedRegion: region });
        },

        setHoveredRegion: (regionId: string | null) => {
          set({ hoveredRegion: regionId });
        },

        toggleControls: () => {
          set((state) => ({ showControls: !state.showControls }));
        },

        toggleDetailModal: (show?: boolean) => {
          set((state) => ({ 
            showDetailModal: show !== undefined ? show : !state.showDetailModal 
          }));
        },

        toggleFullscreen: () => {
          set((state) => ({ isFullscreen: !state.isFullscreen }));
        },

        // ç­›é€‰å’Œæœç´¢
        setFilterOptions: (options: Partial<FilterOptions>) => {
          set((state) => ({
            filterOptions: { ...state.filterOptions, ...options }
          }));
        },

        setSearchTerm: (term: string) => {
          set({ searchTerm: term });
        },

        setSorting: (sortBy: MapState['sortBy'], order: MapState['sortOrder'] = 'asc') => {
          set({ sortBy, sortOrder: order });
        },

        clearFilters: () => {
          set({
            filterOptions: {},
            searchTerm: '',
            sortBy: 'name',
            sortOrder: 'asc'
          });
        },

        // è®¡ç®—æ–¹æ³•
        getFilteredRegions: () => {
          const { regions, filterOptions, searchTerm, sortBy, sortOrder } = get();
          
          let filtered = regions;

          // åº”ç”¨æœç´¢
          if (searchTerm) {
            filtered = filtered.filter(region =>
              region.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
              region.manager.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
              region.regionType.toLowerCase().includes(searchTerm.toLowerCase())
            );
          }

          // åº”ç”¨ç­›é€‰
          if (filterOptions.regionTypes && filterOptions.regionTypes.length > 0) {
            filtered = filtered.filter(region =>
              filterOptions.regionTypes!.includes(region.regionType)
            );
          }

          if (filterOptions.managerIds && filterOptions.managerIds.length > 0) {
            filtered = filtered.filter(region =>
              filterOptions.managerIds!.includes(region.manager.id)
            );
          }

          if (filterOptions.institutionStatus && filterOptions.institutionStatus.length > 0) {
            filtered = filtered.filter(region =>
              region.institutions.some(inst =>
                filterOptions.institutionStatus!.includes(inst.status)
              )
            );
          }

          if (filterOptions.dateRange) {
            filtered = filtered.filter(region =>
              region.institutions.some(inst => {
                const date = new Date(inst.establishedDate);
                return date >= filterOptions.dateRange!.start && date <= filterOptions.dateRange!.end;
              })
            );
          }

          // åº”ç”¨æ’åº
          return sortRegions(filtered, sortBy, sortOrder);
        },

        getRegionColor: (regionType: string, institutionCount: number) => {
          const { colorSchemes } = get();
          const maxCount = Math.max(...Object.values(colorSchemes).map(scheme => scheme.maxInstitutions), 1);
          return calculateRegionColor(regionType, institutionCount, maxCount);
        },

        // ç”¨æˆ·æ“ä½œ
        setUserProfile: (profile: UserProfile | null) => {
          set({ userProfile: profile });
        },

        // é”™è¯¯å¤„ç†
        setError: (error: string | null) => {
          set((state) => ({
            loadingState: {
              ...state.loadingState,
              error: error ? {
                code: 'GENERAL_ERROR',
                message: error,
                details: undefined,
                timestamp: Date.now()
              } : null
            }
          }));
        },

        clearError: () => {
          set((state) => ({
            loadingState: { ...state.loadingState, error: null }
          }));
        },

        // åŒºåŸŸæ˜ å°„ç®¡ç†
        setRegionMapping: (mapping: Record<string, string[]>) => {
          set({ regionMapping: mapping });
        },

        getProvinceRegion: (province: string) => {
          const { regionMapping } = get();
          if (!regionMapping) return null;
          
          for (const [regionName, provinces] of Object.entries(regionMapping)) {
            if (provinces.includes(province)) {
              return regionName;
            }
          }
          return null;
        }
      }),
      {
        name: 'map-store',
        partialize: (state) => ({
          // åªæŒä¹…åŒ–éƒ¨åˆ†çŠ¶æ€
          showControls: state.showControls,
          filterOptions: state.filterOptions,
          sortBy: state.sortBy,
          sortOrder: state.sortOrder,
          userProfile: state.userProfile,
          regionMapping: state.regionMapping
        })
      }
    ),
    {
      name: 'map-store'
    }
  )
);

// é€‰æ‹©å™¨ hooks
export const useRegions = () => useMapStore((state) => state.regions);
export const useSelectedRegion = () => useMapStore((state) => state.selectedRegion);
export const useLoadingState = () => useMapStore((state) => state.loadingState);
export const useStatistics = () => useMapStore((state) => state.statistics);
export const useFilteredRegions = () => useMapStore((state) => state.getFilteredRegions());
export const useUIState = () => useMapStore((state) => ({
  showControls: state.showControls,
  showDetailModal: state.showDetailModal,
  isFullscreen: state.isFullscreen,
  hoveredRegion: state.hoveredRegion
}));

// æ“ä½œ hooks
export const useMapActions = () => useMapStore((state) => ({
  loadRegions: state.loadRegions,
  setSelectedRegion: state.setSelectedRegion,
  setHoveredRegion: state.setHoveredRegion,
  toggleControls: state.toggleControls,
  toggleDetailModal: state.toggleDetailModal,
  toggleFullscreen: state.toggleFullscreen,
  setFilterOptions: state.setFilterOptions,
  setSearchTerm: state.setSearchTerm,
  setSorting: state.setSorting,
  clearFilters: state.clearFilters,
  refreshData: state.refreshData,
  setError: state.setError,
  clearError: state.clearError
}));