import type { RegionData, MapDataPoint, Institution, Statistics } from '@/types';

/**
 * å°†åœ°åŒºæ•°æ®è½¬æ¢ä¸ºåœ°å›¾æ•°æ®ç‚¹
 */
export const transformRegionToMapData = (regions: RegionData[]): MapDataPoint[] => {
  return regions.map(region => ({
    name: region.name,
    value: region.institutions.length,
    regionType: region.regionType,
    manager: region.manager.name,
    itemStyle: {
      color: calculateRegionColor(region.regionType, region.institutions.length, getMaxInstitutions(regions))
    }
  }));
};

/**
 * è®¡ç®—åœ°åŒºé¢œè‰²
 */
export const calculateRegionColor = (
  regionType: string,
  institutionCount: number,
  maxCount: number
): string => {
  // é¢„å®šä¹‰çš„é¢œè‰²æ–¹æ¡ˆ
  const colorSchemes: Record<string, { base: string; range: string[] }> = {
    'åä¸œ': {
      base: '#3b82f6',
      range: ['#eff6ff', '#dbeafe', '#bfdbfe', '#93c5fd', '#60a5fa', '#3b82f6', '#2563eb', '#1d4ed8']
    },
    'åå—': {
      base: '#ef4444',
      range: ['#fef2f2', '#fee2e2', '#fecaca', '#fca5a5', '#f87171', '#ef4444', '#dc2626', '#b91c1c']
    },
    'åè¥¿': {
      base: '#22c55e',
      range: ['#f0fdf4', '#dcfce7', '#bbf7d0', '#86efac', '#4ade80', '#22c55e', '#16a34a', '#15803d']
    },
    'ååŒ—': {
      base: '#eab308',
      range: ['#fefce8', '#fef9c3', '#fef08a', '#fde047', '#facc15', '#eab308', '#ca8a04', '#a16207']
    }
  };

  const scheme = colorSchemes[regionType] || colorSchemes['åä¸œ'];
  
  if (maxCount === 0) return scheme.range[0];
  
  // è®¡ç®—é¢œè‰²å¼ºåº¦ï¼ˆ0-1ï¼‰
  const intensity = Math.min(institutionCount / maxCount, 1);
  
  // æ ¹æ®å¼ºåº¦é€‰æ‹©é¢œè‰²
  const colorIndex = Math.floor(intensity * (scheme.range.length - 1));
  
  return scheme.range[colorIndex];
};

/**
 * è·å–æœ€å¤§æœºæ„æ•°é‡
 */
export const getMaxInstitutions = (regions: RegionData[]): number => {
  return Math.max(...regions.map(region => region.institutions.length), 1);
};

/**
 * æŒ‰åœ°åŒºç±»å‹åˆ†ç»„æ•°æ®
 */
export const groupByRegionType = (regions: RegionData[]): Record<string, RegionData[]> => {
  return regions.reduce((groups, region) => {
    const type = region.regionType;
    if (!groups[type]) {
      groups[type] = [];
    }
    groups[type].push(region);
    return groups;
  }, {} as Record<string, RegionData[]>);
};

/**
 * è®¡ç®—ç»Ÿè®¡æ•°æ®
 */
export const calculateStatistics = (regions: RegionData[]): Statistics => {
  const allInstitutions = regions.flatMap(region => region.institutions);
  
  const activeInstitutions = allInstitutions.filter(inst => inst.status === 'active');
  const inactiveInstitutions = allInstitutions.filter(inst => inst.status === 'inactive');
  
  const regionStats = regions.reduce((stats, region) => {
    const type = region.regionType;
    if (!stats[type]) {
      stats[type] = {
        institutionCount: 0,
        managerCount: 0
      };
    }
    stats[type].institutionCount += region.institutions.length;
    stats[type].managerCount += 1;
    return stats;
  }, {} as Record<string, { institutionCount: number; managerCount: number }>);

  const topRegion = regions.reduce((top, region) => 
    region.institutions.length > top.institutions.length ? region : top, 
    regions[0] || { institutions: [], name: '', manager: { name: '' } }
  );

  return {
    totalRegions: regions.length,
    totalInstitutions: allInstitutions.length,
    activeInstitutions: activeInstitutions.length,
    inactiveInstitutions: inactiveInstitutions.length,
    totalManagers: regions.length,
    activeRegions: regions.filter(region => region.institutions.length > 0).length,
    topRegion: {
      name: topRegion.name,
      institutions: { length: topRegion.institutions.length },
      manager: { name: topRegion.manager.name }
    },
    regionStats
  };
};

/**
 * ç­›é€‰æœºæ„æ•°æ®
 */
export const filterInstitutions = (
  institutions: Institution[],
  filters: {
    status?: 'active' | 'inactive';
    dateRange?: { start: Date; end: Date };
    searchTerm?: string;
  }
): Institution[] => {
  return institutions.filter(institution => {
    // çŠ¶æ€ç­›é€‰
    if (filters.status && institution.status !== filters.status) {
      return false;
    }

    // æ—¥æœŸèŒƒå›´ç­›é€‰
    if (filters.dateRange) {
      const establishedDate = new Date(institution.establishedDate);
      if (establishedDate < filters.dateRange.start || establishedDate > filters.dateRange.end) {
        return false;
      }
    }

    // æœç´¢è¯ç­›é€‰
    if (filters.searchTerm) {
      const searchLower = filters.searchTerm.toLowerCase();
      const matchesName = institution.name.toLowerCase().includes(searchLower);
      const matchesAddress = institution.address.toLowerCase().includes(searchLower);
      if (!matchesName && !matchesAddress) {
        return false;
      }
    }

    return true;
  });
};

/**
 * æ’åºåœ°åŒºæ•°æ®
 */
export const sortRegions = (
  regions: RegionData[],
  sortBy: 'name' | 'institutionCount' | 'manager',
  order: 'asc' | 'desc' = 'asc'
): RegionData[] => {
  const sorted = [...regions].sort((a, b) => {
    let comparison = 0;

    switch (sortBy) {
      case 'name':
        comparison = a.name.localeCompare(b.name, 'zh-CN');
        break;
      case 'institutionCount':
        comparison = a.institutions.length - b.institutions.length;
        break;
      case 'manager':
        comparison = a.manager.name.localeCompare(b.manager.name, 'zh-CN');
        break;
    }

    return order === 'desc' ? -comparison : comparison;
  });

  return sorted;
};

/**
 * æ ¼å¼åŒ–æ•°å­—æ˜¾ç¤º
 */
export const formatNumber = (num: number): string => {
  if (num >= 1000000) {
    return (num / 1000000).toFixed(1) + 'M';
  }
  if (num >= 1000) {
    return (num / 1000).toFixed(1) + 'K';
  }
  return num.toString();
};

/**
 * æ ¼å¼åŒ–æ—¥æœŸæ˜¾ç¤º
 */
export const formatDate = (date: Date | string): string => {
  const d = new Date(date);
  return d.toLocaleDateString('zh-CN', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit'
  });
};

/**
 * ç”Ÿæˆå”¯ä¸€ID
 */
export const generateId = (): string => {
  return Date.now().toString(36) + Math.random().toString(36).substr(2);
};

/**
 * æ·±æ‹·è´å¯¹è±¡
 */
export const deepClone = <T>(obj: T): T => {
  if (obj === null || typeof obj !== 'object') {
    return obj;
  }

  if (obj instanceof Date) {
    return new Date(obj.getTime()) as unknown as T;
  }

  if (Array.isArray(obj)) {
    return obj.map(item => deepClone(item)) as unknown as T;
  }

  const cloned = {} as T;
  Object.keys(obj).forEach(key => {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    (cloned as any)[key] = deepClone((obj as Record<string, unknown>)[key]);
  });

  return cloned;
};

/**
 * é˜²æŠ–å‡½æ•°
 */
export const debounce = <T extends (...args: unknown[]) => unknown>(
  func: T,
  wait: number
): ((...args: Parameters<T>) => void) => {
  let timeout: ReturnType<typeof setTimeout>;
  
  return (...args: Parameters<T>) => {
    clearTimeout(timeout);
    timeout = setTimeout(() => func(...args), wait);
  };
};

/**
 * èŠ‚æµå‡½æ•°
 */
export const throttle = <T extends (...args: unknown[]) => unknown>(
  func: T,
  limit: number
): ((...args: Parameters<T>) => void) => {
  let inThrottle: boolean;
  
  return (...args: Parameters<T>) => {
    if (!inThrottle) {
      func(...args);
      inThrottle = true;
      setTimeout(() => inThrottle = false, limit);
    }
  };
};