import React, { useEffect, useRef } from 'react';
import * as echarts from 'echarts';
import type { MapDataPoint, MapEvent } from '@/types';
import { chinaMapData } from '@/data/chinaMap';


interface MapRendererProps {
  data: MapDataPoint[];
  onMapEvent?: (event: MapEvent) => void;
  loading?: boolean;
  className?: string;
}

// ç”Ÿæˆè§†è§‰æ˜ å°„ç‰‡æ®µ
const generateVisualMapPieces = (data: MapDataPoint[]) => {
  if (data.length === 0) return [];
  
  const maxValue = Math.max(...data.map(d => d.value), 1);
  const pieces = [];
  
  // æ— æœºæ„
  pieces.push({
    min: 0,
    max: 0,
    color: '#f0f0f0',
    label: 'æ— æœºæ„'
  });
  
  // æ ¹æ®æœ€å¤§å€¼åŠ¨æ€ç”Ÿæˆåˆ†æ®µ
  const segments = Math.min(6, maxValue); // æœ€å¤š6ä¸ªåˆ†æ®µ
  for (let i = 1; i <= segments; i++) {
    const min = Math.ceil((i - 1) * maxValue / segments) + (i === 1 ? 1 : 0);
    const max = i === segments ? undefined : Math.ceil(i * maxValue / segments);
    
    pieces.push({
      min,
      max,
      color: getSegmentColor(i),
      label: max ? `${min}-${max}å®¶` : `${min}å®¶ä»¥ä¸Š`
    });
  }
  
  return pieces;
};

// è·å–åˆ†æ®µé¢œè‰²
const getSegmentColor = (segment: number): string => {
  const colors = ['#c6e48b', '#7bc96f', '#239a3b', '#196127', '#0d4d1a', '#0a3d14'];
  const index = Math.min(segment - 1, colors.length - 1);
  return colors[index];
};

const MapRenderer: React.FC<MapRendererProps> = ({
  data,
  onMapEvent,
  loading = false,
  className = ''
}) => {
  const chartRef = useRef<HTMLDivElement>(null);
  const chartInstance = useRef<echarts.ECharts | null>(null);

  // åˆå§‹åŒ–å›¾è¡¨
  useEffect(() => {
    if (!chartRef.current) return;

    // åˆ›å»ºå›¾è¡¨å®ä¾‹
    chartInstance.current = echarts.init(chartRef.current);

    // æ³¨å†Œä¸­å›½åœ°å›¾ - ä½¿ç”¨åœ¨çº¿åœ°å›¾æ•°æ®
    const loadChinaMap = async () => {
      try {
        const response = await fetch('https://geo.datav.aliyun.com/areas_v3/bound/100000_full.json');
        const chinaGeoJson = await response.json();
        echarts.registerMap('china', chinaGeoJson);
      } catch (error) {
        console.warn('åœ¨çº¿åœ°å›¾åŠ è½½å¤±è´¥ï¼Œä½¿ç”¨æœ¬åœ°æ•°æ®:', error);
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        echarts.registerMap('china', chinaMapData as any);
      }
    };
    
    loadChinaMap();

    return () => {
      chartInstance.current?.dispose();
    };
  }, []);

  // æ›´æ–°å›¾è¡¨é…ç½®
  useEffect(() => {
    if (!chartInstance.current) return;

    // å»¶è¿Ÿæ¸²æŸ“ï¼Œç¡®ä¿åœ°å›¾æ•°æ®å·²åŠ è½½
    const renderMap = () => {
      const option: echarts.EChartsOption = {
      title: {
        text: 'ä¸­å›½åœ°å›¾ä»ªè¡¨ç›˜',
        left: 'center',
        textStyle: {
          color: '#1f2329',
          fontSize: 18,
          fontWeight: 'bold'
        }
      },
      tooltip: {
        trigger: 'item',
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        formatter: (params: any) => {
          const data = params.data;
          if (!data) return '';
          
          return `
            <div style="padding: 8px;">
              <div style="font-weight: bold; margin-bottom: 4px;">${data.name}</div>
              <div>è´Ÿè´£äºº: ${data.manager}</div>
              <div>æœºæ„æ•°é‡: ${data.value}</div>
              <div>ç®¡ç†åŒºåŸŸ: ${data.regionType}</div>
            </div>
          `;
        },
        backgroundColor: 'rgba(255, 255, 255, 0.95)',
        borderColor: '#e3e6ea',
        borderWidth: 1,
        textStyle: {
          color: '#1f2329',
          fontSize: 12
        }
      },
      visualMap: {
        type: 'piecewise',
        pieces: generateVisualMapPieces(data),
        orient: 'vertical',
        left: 'left',
        top: 'bottom',
        textStyle: {
          color: '#1f2329',
          fontSize: 12
        },
        itemWidth: 20,
        itemHeight: 14,
        itemGap: 5
      },

      series: [
        {
          name: 'æœºæ„åˆ†å¸ƒ',
          type: 'map',
          map: 'china',
          data: data,
          roam: true,
          scaleLimit: {
            min: 0.8,
            max: 3
          },
          itemStyle: {
            borderColor: '#333',
            borderWidth: 0.5,
            areaColor: '#f0f0f0'
          },
          emphasis: {
            itemStyle: {
              areaColor: '#ffd700',
              shadowBlur: 10,
              shadowColor: 'rgba(0, 0, 0, 0.3)',
              borderWidth: 1,
              borderColor: '#ff6b35'
            }
          },
          select: {
            itemStyle: {
              areaColor: '#ff6b35',
              borderWidth: 1,
              borderColor: '#ff4500'
            }
          }
        }
      ]
    };

    // è®¾ç½®åŠ è½½çŠ¶æ€
    if (loading) {
      chartInstance.current?.showLoading('default', {
        text: 'åŠ è½½ä¸­...',
        color: '#3370ff',
        textColor: '#1f2329',
        maskColor: 'rgba(255, 255, 255, 0.8)'
      });
    } else {
      chartInstance.current?.hideLoading();
    }

    // åº”ç”¨é…ç½®
    chartInstance.current?.setOption(option, true);

    // ç»‘å®šäº‹ä»¶
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const handleClick = (params: any) => {
      if (params.data && onMapEvent) {
        onMapEvent({
          type: 'click',
          data: {
            regionId: params.data.name,
            regionName: params.data.name,
            institutionCount: params.data.value,
            manager: params.data.manager
          },
          timestamp: Date.now()
        });
      }
    };

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const handleMouseOver = (params: any) => {
      if (params.data && onMapEvent) {
        onMapEvent({
          type: 'hover',
          data: {
            regionId: params.data.name,
            regionName: params.data.name,
            institutionCount: params.data.value,
            manager: params.data.manager
          },
          timestamp: Date.now()
        });
      }
    };

    chartInstance.current?.on('click', handleClick);
    chartInstance.current?.on('mouseover', handleMouseOver);

    return () => {
      chartInstance.current?.off('click', handleClick);
      chartInstance.current?.off('mouseover', handleMouseOver);
    };
    };

    // å»¶è¿Ÿæ‰§è¡Œæ¸²æŸ“
    setTimeout(renderMap, 100);
  }, [data, loading, onMapEvent]);

  // å“åº”å¼è°ƒæ•´å’Œé”®ç›˜äº‹ä»¶
  useEffect(() => {
    const handleResize = () => {
      chartInstance.current?.resize();
    };

    const handleKeyDown = (event: KeyboardEvent) => {
      if (!chartInstance.current) return;

      switch (event.key) {
        case 'Escape':
          // é‡ç½®åœ°å›¾è§†å›¾
          chartInstance.current.dispatchAction({
            type: 'restore'
          });
          break;
        case '+':
        case '=':
          // æ”¾å¤§
          chartInstance.current.dispatchAction({
            type: 'geoZoom',
            zoom: 1.2
          });
          break;
        case '-':
          // ç¼©å°
          chartInstance.current.dispatchAction({
            type: 'geoZoom',
            zoom: 0.8
          });
          break;
        case 'r':
        case 'R':
          // é‡ç½®åˆ°åˆå§‹çŠ¶æ€
          chartInstance.current.dispatchAction({
            type: 'geoRestore'
          });
          break;
      }
    };

    window.addEventListener('resize', handleResize);
    window.addEventListener('keydown', handleKeyDown);
    
    return () => {
      window.removeEventListener('resize', handleResize);
      window.removeEventListener('keydown', handleKeyDown);
    };
  }, []);

  return (
    <div 
      ref={chartRef} 
      className={`w-full h-full min-h-[600px] ${className}`}
      style={{ 
        minHeight: '600px',
        width: '100%',
        height: '100%'
      }}
    />
  );
};

export default MapRenderer;